import {__} from '@wordpress/i18n';

import {
    PanelBody,
    BaseControl,
    SelectControl,
    RangeControl,
    ToggleControl,
    Button,
    TextControl,
    Dashicon,
    Notice,
    Tip,
    TextareaControl,
    __experimentalBoxControl as BoxControl,
} from '@wordpress/components';
import {
    MediaUploadCheck,
    MediaUpload,
    RichText,
    PanelColorSettings,
    InspectorControls,
    useBlockProps
} from '@wordpress/block-editor';

export default function Edit(props) {

    const {attributes, setAttributes, clientId} = props;

    const {
        blockId,
        heading,
        headingHTMLTag,
        tocAdded,
        headingColor,
        darkHeadingColor,
        desktopHeadingSize,
        tabletHeadingSize,
        mobileHeadingSize,
        description,
        descriptionColor,
        darkDescriptionColor,
        desktopDescriptionSize,
        tabletDescriptionSize,
        mobileDescriptionSize,
        image,
        imageURL,
        imageAlt,
        desktopImageSize,
        tabletImageSize,
        mobileImageSize,
        file,
        checkBoxText,
        buttonLabel,
        shadow,
        borderStyle,
        borderColor,
        darkBorderColor,
        borderWidth,
        borderRadius,
        background,
        darkBackground,
        desktopPadding,
        tabletPadding,
        mobilePadding
    } = attributes;

    const setAttribute = (name, value) => {
        setAttributes({[name]: value});
    };

    function setFileAttribute(value) {
        setAttributes({['file']: value.url});
        setAttributes({['blockId']: clientId});
    }

    function removeFileAttribute() {
        setAttributes({['file']: ''});
    }

    function setMediaAttribute(name, value) {
        setAttributes({[name]: value.url});
        setAttributes({[name + 'Alt']: value.alt});
    }

    function removeMediaAttribute(name) {
        setAttributes({[name]: ''});
        setAttributes({[name + 'Alt']: ''});
    }

    const headingTagOptions = [
        {label: __('H2'), value: 'h2'},
        {label: __('H3'), value: 'h3'},
        {label: __('H4'), value: 'h4'},
        {label: __('H5'), value: 'h5'},
        {label: __('H6'), value: 'h6'},
        {label: __('p'), value: 'p'},
        {label: __('Div'), value: 'div'},
    ];

    let wrapperClassName = 'gb-wrap gb-download';

    if (shadow) {
        wrapperClassName += ' yes-shadow';
    }

    const blockProps = useBlockProps({
        className: wrapperClassName,
        style: {
            ...(headingColor && {'--heading-color': headingColor}),
            ...(darkHeadingColor && {'--dark-heading-color': darkHeadingColor}),
            ...(desktopHeadingSize && {'--desktop-heading-size': `${desktopHeadingSize}px`}),
            ...(tabletHeadingSize && {'--tablet-heading-size': `${tabletHeadingSize}px`}),
            ...(mobileHeadingSize && {'--mobile-heading-size': `${mobileHeadingSize}px`}),
            ...(descriptionColor && {'--description-color': descriptionColor}),
            ...(darkDescriptionColor && {'--dark-description-color': darkDescriptionColor}),
            ...(desktopDescriptionSize && {'--desktop-description-size': `${desktopDescriptionSize}px`}),
            ...(tabletDescriptionSize && {'--tablet-description-size': `${tabletDescriptionSize}px`}),
            ...(mobileDescriptionSize && {'--mobile-description-size': `${mobileDescriptionSize}px`}),
            ...(desktopImageSize && {'--desktop-image-size': `${desktopImageSize}px`}),
            ...(tabletImageSize && {'--tablet-image-size': `${tabletImageSize}px`}),
            ...(mobileImageSize && {'--mobile-image-size': `${mobileImageSize}px`}),
            ...(borderStyle && {'--border-style': borderStyle}),
            ...(borderRadius && {'--border-radius': `${borderRadius}px`}),
            ...(borderWidth && {'--border-width': `${borderWidth.top || 0} ${borderWidth.right || 0} ${borderWidth.bottom || 0} ${borderWidth.left || 0}`}),
            ...(borderColor && {'--border-color': borderColor}),
            ...(darkBorderColor && {'--dark-border-color': darkBorderColor}),
            ...(background && {'--bg': background}),
            ...(darkBackground && {'--dark-bg': darkBackground}),
            ...(desktopPadding && {'--desktop-padding': `${desktopPadding.top || 0} ${desktopPadding.right || 0} ${desktopPadding.bottom || 0} ${desktopPadding.left || 0}`}),
            ...(tabletPadding && {'--tablet-padding': `${tabletPadding.top || 0} ${tabletPadding.right || 0} ${tabletPadding.bottom || 0} ${tabletPadding.left || 0}`}),
            ...(mobilePadding && {'--mobile-padding': `${mobilePadding.top || 0} ${mobilePadding.right || 0} ${mobilePadding.bottom || 0} ${mobilePadding.left || 0}`}),
        },
    });

    const borderStyleOptions = [
        {label: __('None'), value: 'none'},
        {label: __('Solid'), value: 'solid'},
        {label: __('Dashed'), value: 'dashed'},
        {label: __('Dotted'), value: 'dotted'},
        {label: __('Double'), value: 'double'},
    ];

    return (
        <div {...blockProps}>
            <InspectorControls>
                <PanelBody title={__('Details')} initialOpen={true}>
                    <BaseControl>
                        <Notice status="warning" isDismissible={false}>
                            {__('This block requires MC4WP to be installed and properly set up in order to work.')}
                        </Notice>
                    </BaseControl>
                    <BaseControl>
                        <Tip> {__('Please navigate to "Dashboard > MC4WP > Integrations > Foxiz Subscribe" to select a mailchimp list.')} </Tip>
                    </BaseControl>
                    <SelectControl
                        label={__('Heading Tag')}
                        value={headingHTMLTag}
                        options={headingTagOptions}
                        onChange={(value) => setAttribute('headingHTMLTag', value)}
                    />
                    <ToggleControl
                        label={__('Table of Content Included')}
                        checked={tocAdded}
                        onChange={(value) => setAttribute('tocAdded', value)}
                    />
                    <BaseControl label={__('Upload Image')}>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setMediaAttribute('image', value)}
                                allowedTypes={['image']}
                                value={image}
                                render={({open}) => (
                                    <div>
                                        {!image && (
                                            <Button className="button button-large" onClick={open}>
                                                {__('Add Image')}
                                            </Button>
                                        )}
                                        {image && (
                                            <div>
                                                <img src={image} alt="image"/>
                                                <Button className="button button-large" onClick={() => removeMediaAttribute('image')}>
                                                    {__('Remove Image')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </BaseControl>
                    <TextareaControl
                        label={__('or Enter Custom Link')}
                        value={imageURL}
                        onChange={(value) => setAttribute('imageURL', value)}
                        placeholder={__('//website.com/.../image.jpg')}
                    />
                    <TextControl
                        value={buttonLabel}
                        label={__('Button Label')}
                        onChange={(value) => setAttribute('buttonLabel', value)}
                    />
                    <BaseControl>
                        <Tip>{__('If you enable \'Double opt-in?\' in the MC4WP Foxiz Download Integration, the checkbox will appear below the submit form.')}</Tip>
                    </BaseControl>
                    <TextareaControl
                        label={__('Checkbox Text')}
                        value={checkBoxText}
                        onChange={(value) => setAttribute('checkBoxText', value)}
                    />
                </PanelBody>
                <PanelBody title={__('Download File')} initialOpen={false}>
                    <BaseControl label={__('Select or Upload File')}>
                        <MediaUploadCheck>
                            <MediaUpload
                                onSelect={(value) => setFileAttribute(value)}
                                value={file}
                                render={({open}) => (
                                    <div>
                                        {!file && (
                                            <Button className="button button-large" onClick={open}>
                                                {__('Add File')}
                                            </Button>
                                        )}
                                        {file && (
                                            <div>
                                                <Dashicon icon="media-archive rb-file-upload-icon"/>
                                                <span className="rb-file-upload-url">{file}</span>
                                                <Button className="button button-large" onClick={() => removeFileAttribute()}>
                                                    {__('Remove File')}
                                                </Button>
                                            </div>
                                        )}
                                    </div>
                                )}
                            />
                        </MediaUploadCheck>
                    </BaseControl>
                </PanelBody>
            </InspectorControls>
            <InspectorControls group="styles">
                <PanelBody title={__('Heading')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopHeadingSize}
                                onChange={(value) => setAttribute('desktopHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletHeadingSize}
                                onChange={(value) => setAttribute('tabletHeadingSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileHeadingSize}
                                onChange={(value) => setAttribute('mobileHeadingSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: headingColor,
                                onChange: (value) => setAttribute('headingColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkHeadingColor,
                                onChange: (value) => setAttribute('darkHeadingColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Description')} initialOpen={false}>
                    <label className="rb-control-label">{__('Font Size')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                value={desktopDescriptionSize}
                                onChange={(value) => setAttribute('desktopDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                value={tabletDescriptionSize}
                                onChange={(value) => setAttribute('tabletDescriptionSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                value={mobileDescriptionSize}
                                onChange={(value) => setAttribute('mobileDescriptionSize', value)}
                            />
                        </div>
                    </div>
                    <PanelColorSettings
                        title={__('Color')}
                        colorSettings={[
                            {
                                value: descriptionColor,
                                onChange: (value) => setAttribute('descriptionColor', value),
                                label: __('Color'),
                            },
                            {
                                value: darkDescriptionColor,
                                onChange: (value) => setAttribute('darkDescriptionColor', value),
                                label: __('Dark Mode - Color'),
                            },
                        ]}
                        enableAlpha={true}
                    />
                </PanelBody>
                <PanelBody title={__('Image')} initialOpen={false}>
                    <label className="rb-control-label">{__('Image Width')}</label>
                    <div className="responsive-settings">
                        <div>
                            <label>
                                <Dashicon icon="desktop"/>
                                {__('Desktop')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="150"
                                value={desktopImageSize}
                                onChange={(value) => setAttribute('desktopImageSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="tablet"/>
                                {__('Tablet')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="100"
                                value={tabletImageSize}
                                onChange={(value) => setAttribute('tabletImageSize', value)}
                            />
                        </div>
                        <div>
                            <label>
                                <Dashicon icon="smartphone"/>
                                {__('Mobile')}
                            </label>
                            <TextControl
                                type="number"
                                placeholder="100"
                                value={mobileImageSize}
                                onChange={(value) => setAttribute('mobileImageSize', value)}
                            />
                        </div>
                    </div>
                </PanelBody>
                <PanelBody title={__('Border & Shadow')} initialOpen={false}>
                    <SelectControl
                        label={__('Border Style')}
                        value={borderStyle}
                        options={borderStyleOptions}
                        onChange={(value) => setAttribute('borderStyle', value)}
                        help={__('Please select the border color to see the border if you enable it.')}
                    />
                    {borderStyle && borderStyle !== 'none' && (
                        <>
                            <BoxControl
                                resetValues={{top: null, right: null, bottom: null, left: null}}
                                label={__('Border Width')}
                                values={borderWidth}
                                onChange={(value) => setAttribute('borderWidth', value)}
                            />
                            <PanelColorSettings
                                title={__('Border Color')}
                                colorSettings={[
                                    {
                                        value: borderColor,
                                        onChange: (value) => setAttribute('borderColor', value),
                                        label: __('Color'),
                                    },
                                    {
                                        value: darkBorderColor,
                                        onChange: (value) => setAttribute('darkBorderColor', value),
                                        label: __('Dark Mode - Color'),
                                    },
                                ]}
                            />
                        </>
                    )}
                    <RangeControl
                        label={__('Border Radius')}
                        value={borderRadius}
                        onChange={(value) => setAttribute('borderRadius', value)}
                        step={1}
                        max={100}
                        min={0}
                    />
                    <ToggleControl
                        label={__('Shadow')}
                        checked={shadow}
                        onChange={(value) => setAttribute('shadow', value)}
                    />
                    <PanelColorSettings
                        title={__('Background')}
                        colorSettings={[
                            {
                                value: background,
                                onChange: (value) => setAttribute('background', value),
                                label: __('Background'),
                            },
                            {
                                value: darkBackground,
                                onChange: (value) => setAttribute('darkBackground', value),
                                label: __('Dark Mode - Background'),
                            },
                        ]}
                    />
                </PanelBody>
                <PanelBody title={__('Inner Padding')} initialOpen={false}>
                    <BaseControl><Tip>{__('These settings will apply to block wrapper. Click outside to see the update!')}</Tip></BaseControl>
                    <div className="res-padding-control">
                        <BoxControl
                            resetValues={{top: '30px', right: '30px', bottom: '30px', left: '30px'}}
                            label={<><Dashicon icon="desktop"/> {__('Desktop')}</>}
                            values={desktopPadding}
                            onChange={(value) => setAttribute('desktopPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '25px', right: '25px', bottom: '25px', left: '25px'}}
                            label={<><Dashicon icon="tablet"/> {__('Tablet')}</>}
                            values={tabletPadding}
                            onChange={(value) => setAttribute('tabletPadding', value)}
                        />
                        <BoxControl
                            resetValues={{top: '20px', right: '20px', bottom: '20px', left: '20px'}}
                            label={<><Dashicon icon="smartphone"/> {__('Mobile')}</>}
                            values={mobilePadding}
                            onChange={(value) => setAttribute('mobilePadding', value)}
                        />
                    </div>
                </PanelBody>
            </InspectorControls>
            <div className="gb-download-inner">
                <div className="gb-download-header">
                    {image ? (
                        <img className="gb-image" src={image} alt="Download Image"/>
                    ) : imageURL ? (
                        <img className="gb-image" src={imageURL} alt="Download Image"/>
                    ) : null}
                    <div className="gb-download-header">
                        <RichText
                            tagName={headingHTMLTag || 'h3'}
                            value={heading}
                            onChange={(value) => setAttribute('heading', value)}
                            className="gb-heading"
                            placeholder="Enter heading text..."
                        />
                        <RichText
                            tagName="span"
                            value={description}
                            onChange={(value) => setAttribute('description', value)}
                            className="gb-description"
                            placeholder="Enter description text..."
                        />
                    </div>
                </div>
            </div>
            <div className="download-button-holder h4">{buttonLabel}</div>
        </div>
    )
}
